<template>
  <div class="d-flex flex-column justify-center align-center">
    
    <v-toolbar flat>
      <v-toolbar-title> </v-toolbar-title>
    </v-toolbar>

    <div class="d-flex flex-column justify-center align-center">
      <div class="thumbnail-wrapper">
        <img v-if="host.userThumbnail" class="thumbnail" :src="host.userThumbnail">
        <img v-if="!host.userThumbnail" class="thumbnail" src="../../../public/mystudy/userprofile/default.jpg">
      </div>

      <v-text-field
        v-model="host.userEmail"
        label=""
        type="text"
        style="width:60%"
        disabled
      ></v-text-field>
      <v-text-field
        v-model="host.userName"
        label=""
        type="text"
        style="width:60%"
        clearable
        required
      ></v-text-field>

      <v-autocomplete
        ref=""
        v-model="host.major"
        :items="majors"
        label=""
        style="width:60%"
      ></v-autocomplete>

      <v-select
        v-model="form.education"
        :items="education"
        label=" "
        style="width:60%"
      ></v-select>

      <!--   -->
      <!-- / / / -->
      <v-select
        v-model="form.status"
        :items="status"
        label=" "
        style="width:60%"
      ></v-select>

      <v-text-field
        v-if="form.status == ' '"
        v-model="host.field1"
        label=" "
        type="text"
        style="width:60%"
      ></v-text-field>

      <v-card flat width="60%">
        <v-card-title> </v-card-title>
        <v-autocomplete
          v-model="host.desiredField1"
          :items="desiredFields"
          label="1"
        ></v-autocomplete>
        <v-autocomplete
          v-model="host.desiredField2"
          :items="desiredFields"
          label="2"
        ></v-autocomplete>
        <v-autocomplete
          v-model="host.desiredField3"
          :items="desiredFields"
          label="3"
        ></v-autocomplete>
      </v-card>
      
      <div class="my-2" style="width:60%">
        <v-btn @click="update" block large color="primary"> </v-btn>
      </div>

    </div>
</div>
</template>

<script>
import axios from "axios"

export default {
  name: "EditProfile",
  data(){
    const defaultForm = Object.freeze({
        education: '',
        status: '',
      })
    return{
      loginUID : this.$route.params.UID,
      host: {},
      form: Object.assign({}, defaultForm),
      education: [' ', '', '(2)', '(4) ', ' '],
      status: ['', ' ', ' ', ''],
      majors: ['', '', '', '', '', '', '', ''],
      desiredFields: ['', '', ''],
    }
  },
  created() {
    // UID   
    axios.post("http://localhost:8080/getUser", {
      id: this.loginUID
    })
    .then(res => {
        console.log("getUser Success.")
        // console.log(res.data)
        this.host = res.data.object
    })
    .catch( function(error) {
        // console.log(this.hostUID)
        console.log(error)
    })
  },
  methods: {
    update() {
      axios.post('http://localhost:8080/updateMyInfo', this.host)
      .then( res => {
        console.log(res) 
      })
      .catch( res => {
        console.log(res)
      })
    }
  }
}
</script>

