<template>
  <div class="p-4">
    <!--      ->   -->
    <v-switch v-model="switch1" :label="`${ licenseInfo.licenseName }  `"></v-switch>

    <div v-show="switch1">
      <v-form ref="form" v-model="valid" lazy-validation>
        <span>   ?</span>
        <!--   -->
        <v-rating
          v-model="rating"
          :length="10"
          :hover="true"
          :readonly="false"
          :size="size"
          :dense="false"
          :color="color"
          :background-color="bgColor"
        ></v-rating>

        <!--  -->
        <v-slider
          v-model="reviewHours"
          color="green"
          label="  "
          min="1"
          max="24"

          thumb-label
        ></v-slider>

        <!--  -->
        <v-slider
          v-model="reviewDuration"
          color="orange"
          label=" "
          min="1"
          max="50"
          thumb-label
        ></v-slider>

        <!--    -->
        <v-text-field
          v-model="reviewContent"
          :counter="255"
          :rules="reviewRules"
          label="  "
          required
        ></v-text-field>

        <!--    -->
        <v-btn :disabled="!valid" color="primary" class="mr-4" @click="validate"></v-btn>
      </v-form>
    </div>

    <!--    -->
    <span>    !</span>
    <hr>
    <ul v-for="reviewArr in reviewArray" :key="reviewArr.key">
      <li>: {{ reviewArr.reviewWriter.userName }}</li>
      <li>: {{ reviewArr.reviewContents }}</li>  
      <li>: {{ reviewArr.reviewDuration }}</li>
      <li>: {{ reviewArr.reviewHours }}</li>
      <li>: {{ reviewArr.reviewRating }}</li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: "LicenseReview",
  props: {
    licenseInfo: {
      type: Object,
    },
  },
  mounted: function() {
    axios.get('http://i3a102.p.ssafy.io:8080/license/getReview', {
      params: {
        "licenseCode": this.licenseInfo.licenseCode
      }
    })
      .then(res => this.reviewArray = res.data.object)
      .catch(err => console.log(err.message))
  },
  methods: {
    validate() {
      this.$refs.form.validate();
      console.log('send review started')
      console.log(this.licenseInfo.licenseCode)
      console.log(this.reviewHours)
      console.log(this.rating)
      console.log(this.reviewContent)
      console.log(this.reviewDuration)
      console.log(this.uid)
      axios.post("http://i3a102.p.ssafy.io:8080/license/addReview", {
        
          "licenseCode": this.licenseInfo.licenseCode,
          "reviewHours": this.reviewHours,
          "reviewRating": this.rating,
          "reviewContents": this.reviewContent,
          "reviewDuration": this.reviewDuration,
          //   
          "uid": this.uid,
      })
        .then( res => {
          console.log( res.data )
          this.reviewArray = res.data.object
        })
        .catch(err => console.log( err.message ))
        
    },
  },
  watch: {
    switch1: function () {
      this.rating = 0;
      this.reviewContent = ""
      this.reviewDuration = null
      this.reviewHours = null
    },
  },
  data: function () {
    return {
      //   
      uid: 1,
      switch1: false,
      rating: 0,
      reviewHours: null,
      reviewDuration: null,
      reviewContent: "",
      //   
      color: "yellow darken-3",
      bgColor: "yellow darken-2",
      // validate    
      reviewRules: [
        (v) => !!v || "  ",
        (v) =>
          (v && v.length <= 255) || " 255    .",
      ],
      reviewArray: {
        type: Array
      }
    };
  },
};
</script>

